      <p>ËÆ©ÈöèÊú∫Êù•Â∏Æ‰Ω†ÂÅöÂÜ≥ÂÆöÔºåÊîØÊåÅÂèØËßÜÂåñÊü•ÁúãÁªìÊûúÂèòÂåñ</p>
    </div>

    <!-- ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢ -->
    <div class="mobile-view-toggle" v-if="isMobile">
      <el-radio-group v-model="activeView" @change="handleViewChange">
        <el-radio-button label="input">üìù ËÆæÁΩÆÈÄâÈ°π</el-radio-button>
        <el-radio-button label="result">üéØ Êü•ÁúãÁªìÊûú</el-radio-button>
        <el-radio-button label="stats">üìä ÁªüËÆ°ÂàÜÊûê</el-radio-button>
      </el-radio-group>
    </div>

    <el-row :gutter="20" class="main-row">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="input-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'input' }">
        <el-card class="input-card">
          <div slot="header">
            <span>ÂÜ≥ÂÆöÈÄâÈ°π</span>
          </div>
          
          <el-form label-width="100px">
            <el-form-item label="ÈÄâÈ°πÂàóË°®">
              <el-input
                type="textarea"
                v-model="optionsText"
                placeholder="ËØ∑ËæìÂÖ•ÈÄâÈ°πÔºåÊØèË°å‰∏Ä‰∏™ÔºåÂ¶ÇÔºö&#10;ÂéªÁúãÁîµÂΩ±&#10;Âú®ÂÆ∂‰ºëÊÅØ&#10;Âá∫ÂéªÂêÉÈ•≠&#10;Á∫¶ÊúãÂèãËÅö‰ºö"
                :rows="6"
                @input="parseOptions"
              ></el-input>
            </el-form-item>

            <el-form-item label="ÂÜ≥ÂÆöÊ¨°Êï∞">
              <el-input-number
                v-model="decisionCount"
                :min="1"
                :max="100000"
                style="width: 100%"
              ></el-input-number>
            </el-form-item>

            <el-form-item label="Âä®ÁîªÈÄüÂ∫¶">
              <el-slider
                v-model="animationSpeed"
                :min="1"
                :max="5"
                :marks="{ 1: 'ÂæàÊÖ¢', 2: 'ÊÖ¢', 3: 'Ê≠£Â∏∏', 4: 'Âø´', 5: 'ÂæàÂø´' }"
              ></el-slider>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="startDecision" :disabled="isRunning || options.length < 2">
                <i class="el-icon-video-play"></i> ÂºÄÂßãÂÜ≥ÂÆö
              </el-button>
              <el-button @click="stopDecision" :disabled="!isRunning">
                <i class="el-icon-video-pause"></i> ÂÅúÊ≠¢
              </el-button>
              <el-button @click="clearResults">
                <i class="el-icon-delete"></i> Ê∏ÖÁ©∫ÁªìÊûú
              </el-button>
              <el-button @click="loadSample">
                <i class="el-icon-document"></i> Á§∫‰æã
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card class="current-result-card" style="margin-top: 20px;" :class="{ 'mobile-hidden': isMobile && activeView !== 'result' }">
          <div slot="header">
            <span>ÂΩìÂâçÁªìÊûú</span>
          </div>

          <div class="current-result" v-if="currentResult">
            <div class="result-display">
              <div class="result-text">{{ currentResult }}</div>
              <div class="result-count">Á¨¨ {{ currentDecisionIndex }} Ê¨°ÂÜ≥ÂÆö</div>
            </div>
          </div>

          <div v-else class="no-result">
            <i class="el-icon-question"></i>
            <p>ÁÇπÂáªÂºÄÂßãÂÜ≥ÂÆöÊåâÈíÆ</p>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="stats-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'stats' }">
        <el-card class="stats-card">
          <div slot="header">
            <span>ÁªìÊûúÁªüËÆ°Ë°®</span>
            <div style="float: right;">
              <el-button size="mini" @click="exportResults" :disabled="statistics.length === 0">
                <i class="el-icon-download"></i> ÂØºÂá∫
              </el-button>
            </div>
          </div>

          <div class="stats-content">
            <div v-if="statistics.length > 0" class="stats-list">
              <div v-for="stat in statistics" :key="stat.option" class="stat-item">
                <div class="stat-option">{{ stat.option }}</div>
                <div class="stat-info">
                  <span class="stat-count">{{ stat.count }} Ê¨°</span>
                  <div class="stat-bar">
                    <div class="stat-fill" :style="{ width: stat.percentage + '%' }"></div>
                  </div>
                  <span class="stat-percentage">{{ stat.percentage }}%</span>
                </div>
              </div>
            </div>
            <div v-else class="no-stats">
              <i class="el-icon-pie-chart"></i>
              <p>ÂºÄÂßãÂÜ≥ÂÆöÂêéÂ∞ÜÊòæÁ§∫ÁªüËÆ°Êï∞ÊçÆ</p>
            </div>
          </div>
        </el-card>


      </el-col>
    </el-row>

    <el-card class="history-card" style="margin-top: 20px;" :class="{ 'mobile-hidden': isMobile && activeView !== 'stats' }">
      <div slot="header">
        <span>ÂÜ≥ÂÆöÂéÜÂè≤</span>
        <div style="float: right;">
          <el-button size="mini" @click="clearHistory" :disabled="results.length === 0">
            <i class="el-icon-delete"></i> Ê∏ÖÁ©∫ÂéÜÂè≤
          </el-button>
        </div>
      </div>
      
      <div class="history-list" v-if="results.length > 0">
        <div v-for="(result, index) in results.slice().reverse()" :key="index" class="history-item">
          <span class="history-index">{{ results.length - index }}</span>
          <span class="history-result">{{ result }}</span>
          <span class="history-time">{{ formatTime(new Date()) }}</span>
        </div>
      </div>
      
      <div v-else class="no-history">
        <p>ÊöÇÊó†ÂÜ≥ÂÆöÂéÜÂè≤</p>
      </div>
    </el-card>
  </div>
</template>

<script>
import DownloadUtil from '@/util/DownloadUtil'
import DeviceUtil from '@/util/DeviceUtil'

export default {
  name: 'DecisionMakerView',
  metaInfo: {
    title: 'ÈöèÊú∫ÂÜ≥ÂÆöÂô® - Â∏Æ‰Ω†ÂÅöÂÜ≥ÂÆöÁöÑÂ∑•ÂÖ∑',
    meta: [
      { name: 'description', content: 'ÈöèÊú∫ÂÜ≥ÂÆöÂô®Â∏Æ‰Ω†Âú®Â§ö‰∏™ÈÄâÈ°π‰∏≠ÂÅöÂá∫ÈÄâÊã©ÔºåÊîØÊåÅÂèØËßÜÂåñÂõæË°®Â±ïÁ§∫ÁªìÊûúÁªüËÆ°„ÄÇ' },
      { name: 'keywords', content: 'ÈöèÊú∫ÂÜ≥ÂÆö,ÂÅöÂÜ≥ÂÆö,ÈÄâÊã©Âõ∞ÈöæÁóá,ÈöèÊú∫ÈÄâÊã©,ÂÜ≥Á≠ñÂ∑•ÂÖ∑' }
    ]
  },
  data() {
    return {
      optionsText: '',
      options: [],
      decisionCount: 10,
      animationSpeed: 3,
      isRunning: false,
      currentResult: '',
      currentDecisionIndex: 0,
      results: [],
      statistics: [],
      timer: null,
      isMobile: false,
      activeView: 'input'
    }
  },
  mounted() {
    this.loadSample()
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer)
    }
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = DeviceUtil.isMobile()
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },

    parseOptions() {
      this.options = this.optionsText
        .split('\n')
        .map(option => option.trim())
        .filter(option => option.length > 0)
    },

    startDecision() {
      if (this.options.length < 2) {
        this.$message.warning('Ëá≥Â∞ëÈúÄË¶Å2‰∏™ÈÄâÈ°πÊâçËÉΩÂºÄÂßãÂÜ≥ÂÆö')
        return
      }

      this.isRunning = true
      this.currentDecisionIndex = 0
      this.results = []
      this.statistics = []

      // ÁßªÂä®Á´ØËá™Âä®ÂàáÊç¢Âà∞ÁªìÊûúËßÜÂõæ
      if (this.isMobile) {
        this.activeView = 'result'
      }

      const interval = Math.max(100, 1100 - this.animationSpeed * 200)

      this.timer = setInterval(() => {
        if (this.currentDecisionIndex >= this.decisionCount) {
          this.stopDecision()
          return
        }

        this.currentDecisionIndex++
        const randomIndex = Math.floor(Math.random() * this.options.length)
        this.currentResult = this.options[randomIndex]
        this.results.push(this.currentResult)

        this.updateStatistics()
      }, interval)
    },

    stopDecision() {
      this.isRunning = false
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }

      if (this.results.length > 0) {
        this.$message.success(`ÂÜ≥ÂÆöÂÆåÊàêÔºÅÂÖ±ËøõË°å‰∫Ü ${this.results.length} Ê¨°ÂÜ≥ÂÆö`)

        // ÁßªÂä®Á´ØËá™Âä®ÂàáÊç¢Âà∞ÁªüËÆ°ËßÜÂõæ
        if (this.isMobile) {
          setTimeout(() => {
            this.activeView = 'stats'
          }, 1000)
        }
      }
    },

    updateStatistics() {
      const counts = {}
      this.results.forEach(result => {
        counts[result] = (counts[result] || 0) + 1
      })

      this.statistics = Object.entries(counts).map(([option, count]) => ({
        option,
        count,
        percentage: Math.round((count / this.results.length) * 100)
      })).sort((a, b) => b.count - a.count)
    },



    clearResults() {
      this.results = []
      this.statistics = []
      this.currentResult = ''
      this.currentDecisionIndex = 0
    },

    clearHistory() {
      this.clearResults()
    },

    loadSample() {
      this.optionsText = `ÂéªÁúãÁîµÂΩ±
Âú®ÂÆ∂‰ºëÊÅØ
Âá∫ÂéªÂêÉÈ•≠
Á∫¶ÊúãÂèãËÅö‰ºö
ÂéªÂÖ¨Âõ≠Êï£Ê≠•
ËØª‰∏ÄÊú¨‰π¶
Â≠¶‰π†Êñ∞ÊäÄËÉΩ
Êï¥ÁêÜÊàøÈó¥`
      this.parseOptions()
    },

    exportResults() {
      if (this.results.length === 0) {
        this.$message.warning('Ê≤°ÊúâÂèØÂØºÂá∫ÁöÑÊï∞ÊçÆ')
        return
      }

      const data = {
        options: this.options,
        results: this.results,
        statistics: this.statistics,
        totalDecisions: this.results.length,
        exportTime: new Date().toISOString()
      }

      const filename = DownloadUtil.generateTimestampFilename('ÂÜ≥ÂÆöÁªìÊûú', 'json')
      DownloadUtil.downloadJSON(data, filename)
      this.$message.success('ÁªìÊûúÂØºÂá∫ÊàêÂäü')
    },

    formatTime(date, format = 'display') {
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      const seconds = String(date.getSeconds()).padStart(2, '0')
      
      if (format === 'file') {
        return `${year}${month}${day}_${hours}${minutes}${seconds}`
      }
      
      return `${hours}:${minutes}:${seconds}`
    }
  }
}
</script>

